import React from 'react';
import { Listener } from '../store';
import { Grid, Typography, Button, Table, TableHead, TableRow, TableCell, TableBody  } from '@material-ui/core';
import { Center, Panel, Hr, Busy, AddButton, BackButton } from '../ui';
import { Link } from 'react-router-dom';

let styles = {
	headerRow: {
		height: '32px',
	},
	row: {
		height: '32px',
		cursor: 'pointer',
	},	
	headerCell: {
		padding: 0,
	},
	cell: {
		padding: 0,
	}
}

export default
class Journal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			journal: null,
		};
	}

	renderPages() {
		if(!this.state.journal)
			return;
		return this.state.journal.pages.map((page) => {
			let status = '';
			let statusStyle = {};
			if(page.error) {
				status = '!!!';
				statusStyle.color = 'red';
			} else if(page.alarm) {
				status = '!';
				statusStyle.color = 'orange';
			}		
			let cellStyle = { ...styles.cell };	
			if(page.estimated)
				cellStyle.color = '#b9b9b9';
			let handleRowClick = (event) => {
				this.props.history.push('/page/'+this.props.match.params.methodic_id+'/'+page.id);
			}
			let handleContextMenu = async (event) => {
				event.preventDefault();
				let action = await this.props.services.messageBox({
					title: 'Вы уверены, что хотите удалить страницу?',
					content: page.name,
					actions: [
						{ value: 'delete', caption: 'Удалить' },
						{ value: 'cancel', caption: 'Отмена' },
					],
				});
				if(action == 'delete')
					this.handleDeletePage(page.id);
			}
			return (
				<TableRow hover key={page.id} style={styles.row} onClick={handleRowClick} onContextMenu={handleContextMenu}>
					<TableCell style={cellStyle}>{page.name}</TableCell>
					<TableCell style={cellStyle}>{page.probes}</TableCell>
					<TableCell style={{ ...cellStyle, ...statusStyle }}>{status}</TableCell>					
				</TableRow>
			);
		});		
	}	

	render() {
		let renderedPages = this.renderPages();
		return <>
			<Busy show={this.state.journal == null} block />
			<Listener reload name={'journal.'+this.props.match.params.methodic_id} onChange={this.handleJournalChange} />
			<Center hor ver>
				<Panel width="360px" maxWidth="100%">				
					<Center hor><Typography variant="headline">Журнал</Typography></Center>
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Методика'}</Typography>
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{this.state.journal && this.state.journal.methodicName}</b></Typography>	
						</Grid>
					</Grid>					
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Прибор'}</Typography>
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{this.state.journal && this.state.journal.deviceName}</b></Typography>	
						</Grid>
					</Grid>
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<BackButton component={Link} to="/methodics_list" />
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{'Страницы'}</b></Typography>	
						</Grid>
						<Grid item xs={false}>
							<AddButton onClick={this.handleAddClick} />
						</Grid>
					</Grid>					
					<Hr />
					<Table>
						<TableHead>
							<TableRow style={styles.headerRow}>
								<TableCell style={styles.headerCell}>{'Идентификатор'}</TableCell>
								<TableCell style={styles.headerCell}>{'Пробы'}</TableCell>
								<TableCell style={styles.headerCell}>{'Статус'}</TableCell>
							</TableRow>
						</TableHead>
						{renderedPages && renderedPages.length != 0 &&
							<TableBody>
								{renderedPages}
							</TableBody>
						}
					</Table>
				</Panel>
			</Center>
		</>;
	}

	handleJournalChange = (journal) => {
		this.setState({ journal });
	}

	handleDeletePage = async (id) => {
		
	}
};