import React from 'react';
import { Listener } from '../store';
import { Grid, TextField, Typography, Button, Table, TableHead, TableRow, TableCell, TableBody  } from '@material-ui/core';
import { Lock, LockOpen  } from '@material-ui/icons';
import { Center, Panel, Hr, Busy, AddButton, BackButton } from '../ui';
import { Link } from 'react-router-dom';

import css from './styles.css';

let styles = {
	headerRow: {
		height: '32px',
	},
	row: {
		height: '32px',
		cursor: 'pointer',
	},	
	headerCell: {
		padding: 0,
	},
	cell: {
		padding: 0,
	},
	inlineBlock: {
		display: 'inline-block',
	},
	marginLeft: {
		marginLeft: '8px',
	},
	lock: {
		marginRight: '-4px',
		cursor: 'pointer',
	},
}

export default
class Page extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			page: null,
			lock: null,
			comment: null,
		};
	}

	renderProbes() {
		if(!this.state.page)
			return;
		return this.state.page.probes.map((probe) => {
			let status = '';
			let statusStyle = {};
			if(probe.error) {
				status = '!!!';
				statusStyle.color = 'red';
			} else if(probe.alarm) {
				status = '!';
				statusStyle.color = 'orange';
			}		
			let cellStyle = { ...styles.cell };
			if(probe.estimated)
				cellStyle.color = '#b9b9b9';
			let handleRowClick = (event) => {
				//this.props.history.push('/probe/'+this.props.match.params.methodic_id+'/'+this.state.page.id+'/'+probe.id);
			}
			let handleContextMenu = async (event) => {
				event.preventDefault();
				let action = await this.props.services.messageBox({
					title: 'Вы уверены, что хотите удалить пробу?',
					content: probe.name,
					actions: [
						{ value: 'delete', caption: 'Удалить' },
						{ value: 'cancel', caption: 'Отмена' },
					],
				});
				if(action == 'delete')
					this.handleDeleteProbe(probe.id);
			}
			return (
				<TableRow hover key={probe.id} style={styles.row} onClick={handleRowClick} onContextMenu={handleContextMenu}>
					<TableCell style={cellStyle}>{probe.name}</TableCell>
					<TableCell style={{ ...cellStyle, ...statusStyle }}>{status}</TableCell>					
				</TableRow>
			);
		});		
	}	

	render() {
		let renderedProbes = this.renderProbes();
		let lockCheckboxClasses = {
			root: css.lockCheckboxRoot,
		}
		let lock = this.state.lock != null ? this.state.lock : this.state.page && this.state.page.lock;
		let comment = this.state.comment != null ? this.state.comment : this.state.page && this.state.page.comment || '';
		return <>
			<Busy show={this.state.page == null} block />
			<Listener reload name={'page.'+this.props.match.params.page_id} onChange={this.handlePageChange} />
			<Center hor ver>
				<Panel width="360px" maxWidth="100%">				
					<Center hor><Typography variant="headline">{'Страница журнала'}</Typography></Center>
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography style={styles.inlineBlock} variant="body2">{'Дата'}</Typography>
							<Typography style={{ ...styles.inlineBlock,...styles.marginLeft }} variant="body2"><b>{this.state.page && this.state.page.date}</b></Typography>
						</Grid>
						<Grid item xs={false}>
							<Typography style={styles.inlineBlock} variant="body2">{'Смена'}</Typography>							
							<Typography style={{ ...styles.inlineBlock,...styles.marginLeft }} variant="body2"><b>{this.state.page && this.state.page.shiftNo}</b></Typography>
						</Grid>
					</Grid>					
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Button variant="outlined" size="small" component={Link} to={'/calibrations/'+this.props.match.params.methodic_id+'/'+this.props.match.params.page_id}>{'Градуировки'}</Button>	
						</Grid>
						<Grid item xs={false}>
							<Button variant="fab" mini onClick={this.handleLockClick}>{lock ? <Lock /> : <LockOpen />}</Button>
						</Grid>
					</Grid>
					<TextField fullWidth multiline rows={1} rowsMax={5} label="Примечание" margin="dense" value={comment} onChange={this.handleCommentChange} />							
					<br /><br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<BackButton component={Link} to={'/journal/'+this.props.match.params.methodic_id} />
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{'Пробы'}</b></Typography>	
						</Grid>
						<Grid item xs={false}>
							<AddButton onClick={this.handleAddClick} />
						</Grid>
					</Grid>					
					<Hr />
					<Table>
						<TableHead>
							<TableRow style={styles.headerRow}>
								<TableCell style={styles.headerCell}>{'Наименование'}</TableCell>
								<TableCell style={styles.headerCell}>{'ОП'}</TableCell>
							</TableRow>
						</TableHead>
						{renderedProbes && renderedProbes.length != 0 &&
							<TableBody>
								{renderedProbes}
							</TableBody>
						}
					</Table>
				</Panel>
			</Center>
		</>;
	}

	handlePageChange = (page) => {
		this.setState({ page });
	}

	handleDeleteProbe = async (id) => {
		
	}

	handleLockClick = async (event) => {
		let lock = this.state.lock != null ? this.state.lock : this.state.page && this.state.page.lock;
		this.setState({ lock: !lock });
	}

	handleCalibrationsClick = (event) => {

	}

	handleCommentChange = (event) => {
		this.setState({ comment: event.target.value });		
	}
};