import EventEmitter from 'eventemitter3';

const noop = function() {};

export default
class Store {
	constructor(options) {
		options = options || {};
		this.delimiter = options.delimiter || '.';
		this.ee = new EventEmitter();
		this.points = {};
		this.loaders = {};
	}

	_point(key,create) {
		let point = this.points[key];
		if(point == null) {
			if(!create)
				return;
			this.points[key] = point = {
				value: undefined,
				cancel: null,
				fresh: true,
			};
		}
		return point;
	}

	_parse(name) {
		if(typeof(name) == 'string') {
			let pos = name.indexOf(this.delimiter);
			return pos != -1 ? {
				name: name.substr(0,pos),
				param: name.substr(pos+1),
				key: name,
			} : {
				name,
				param: undefined,
				key: name,
			}
		} else {
			return {
				name: String(name.name),
				param: name.param != null ? String(name.param) : undefined,
			}
		}
	}

	_load(point,path,loader) {
		let canceled = false;
		let next = (value) => {
			if(canceled)
				return;
			point.value = value;
			if(value !== undefined) {
				point.fresh = false;
				this.ee.emit(path.key,value);
			} else {
				point.fresh = true;
			}
		};
		let cancel = loader(next,path.param,point.value);
		if(typeof(cancel) != 'function')
			cancel = noop;
		point.cancel = () => {
			canceled = true;
			cancel();
		}
	}

	_cancel(point) {
		if(!point.cancel)
			return;
		point.cancel();
		point.cancel = null;
	}

	loader(name,loader) {
		loader ? this.loaders[name] = loader : delete this.loaders[name];
	}

	set(name,value) {
		if(value === undefined)
			return this.delete(name);
		let path = this._parse(name);
		let point = this._point(path.key,true);
		point.value = value;
		this.ee.emit(path.key,value);
	}	

	delete(name) {
		let path = this._parse(name);
		let point = this._point(path.key);
		if(point == null)
			return;
		point.value = undefined;
		if(!point.cancel)
			delete this.points[path.key];
	}

	on(name,callback,context) {
		let path = this._parse(name);
		this.ee.on(path.key,callback,context);
		let loader = this.loaders[path.name];
		let point = this._point(path.key,!!loader);
		if(point == null)
			return;
		if(!point.cancel && loader)
			this._load(point,path,loader);
		if(point.value !== undefined)
			callback.call(context,point.value);
	}

	off(name,callback,context) {
		let path = this._parse(name);
		this.ee.off(path.key,callback,context);
		if(this.ee.listenerCount(path.key) !== 0)
			return;
		let point = this._point(path.key,false);
		if(point == null)
			return;
		this._cancel(point);
		if(point.value === undefined)
			delete this.points[path.key];
	}

	reload(name,force) {
		let path = this._parse(name);
		let loader = this.loaders[path.name];
		let point = this._point(path.key,!!loader);
		if(point == null)
			return;		
		if(!point.fresh || force) {
			point.value = undefined;
			this._cancel(point);
			loader ? this._load(point,path,loader) : delete this.points[path.key];
		}
	}
}
