import React from 'react';
import cn from 'classnames';
import { Paper } from '@material-ui/core';
import css from './styles.css';

export default
class Panel extends React.Component {
	render() {
		let { maxWidth, width, maxHeight, height, ...props } = this.props;
		props.className = cn(css.root,props.className);
		props.style = Object.assign({},props.style,{
			maxWidth,
			width,
			maxHeight,
			height
		});
		return <Paper {...props}>{this.props.children}</Paper>
	}
}