import React from 'react';
import css from './styles.css';

export default
class Crash extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		let stack = [];
		this.props.error.stack.split('\n').forEach(function(line,index) {
			stack.push(line);
			stack.push(<br key={index} />);
		});
		
		return (
			<div className={css.root}>				
				<u><b>{'Произошла ошибка:'}</b></u>
				<br />
				<i>{stack}</i>
			</div>
		)
	}
}