import React from 'react';
import css from './styles.css';
import cn from 'classnames';
import Panel from '../panel';

export default
class Busy extends React.Component {
	constructor(props) {
		super(props);
	}

	refRoot = (node) => {
		if(node && this.props.busyBlockCount)
			node.focus();
	}

	render() {
		if(!this.props.show)
			return null;
		let block = this.props.block;
		return (
			<div className={cn({ [css.root]: true, [css.block]: !!this.props.block })} ref={this.refRoot} tabIndex={block ? 0 : undefined} onClick={this.noop} onKeyDown={this.noop} onKeyUp={this.noop} onKeyPress={this.noop}>
				<div className={css.wrapper}>
					<div className={css.content}>
						<Panel elevation={12}>{'Загрузка...'}</Panel>
					</div>
				</div>
			</div>
		);
	}

	noop = (event) => {
		event.preventDefault();
	}
}
