let requests = {};

requests.login = async (api,context,data) => {
	if(data.login != data.password)
		return false;
	context.session.login = data.login;
	return true;
};

requests.exit = async(api,context) => {
	context.session.login = null;
	await new Promise((resolve) => {
		context.session.destroy(resolve);
	});
}

requests.getUserInfo = async(api,context) => {
	return {
		login: context.session.login,
		loggedIn: context.session.login != null,
	}
};

