import React from 'react';
import { Crash, Busy, MessageBox } from '../ui';

//import Client from '../client';
import Client from '../fake_client';

import { Store, Provider as StoreProvider, createLoader } from '../store';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import withProps from '../with_props';
import mixinChangeState from '../mixin_change_state';

import Login from '../login';
import MethodicsList from '../methodics_list';
import CheckUserInfo from '../check_user_info';
import Journal from '../journal';
import Page from '../page';
import Calibrations from '../calibrations';

export default
@mixinChangeState
class Application extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			error: null,
			busyCount: 0,
			busyBlockCount: 0,
			messageBoxes: [],
		};

		this.services = {};

		this.services.client = new Client(this.props.config.client);
		this.services.busy = this.busy;
		this.services.messageBox = this.messageBox;
		this.services.store = new Store();

		let loaderOptions = { repeatOnErrorTimeout: 1000 };
		this.services.store.loader('userInfo',createLoader(() => this.services.client.request('getUserInfo'),loaderOptions));
		this.services.store.loader('methodics',createLoader(() => this.services.client.request('getMethodics'),loaderOptions));
		this.services.store.loader('journal',createLoader((id) => this.services.client.request('getJournal',id),loaderOptions));
		this.services.store.loader('page',createLoader((id) => this.services.client.request('getPage',id),loaderOptions));
		this.services.store.loader('calibrations',createLoader((id) => this.services.client.request('getCalibrations',id),loaderOptions));

		window.services = this.services;
	}

	componentDidCatch(error) {
		this.setState({ error });
	}

	busy = async (action,options) => {
		options = options || {};
		let block = options.block == null || options.block == true; 
		await this.changeState((state) => { return { busyCount: state.busyCount + 1, busyBlockCount: state.busyBlockCount + block }});
		try {
			return await action();
		} finally {
			await this.changeState((state) => { return { busyCount: state.busyCount - 1, busyBlockCount: state.busyBlockCount - block }});
		}
	}

	messageBox = async (props) => {
		let messageBox = {
			props,
			promise: null,
			resolve: null,
			remove: () => {
				this.setState((state) => {
					let index = state.messageBoxes.indexOf(messageBox);
					if(index != -1) {
						let messageBoxes = state.messageBoxes.slice();
						messageBoxes.splice(index,1);
						return { messageBoxes };
					}
				});
			}
		};
		messageBox.promise = new Promise((resolve,reject) => messageBox.resolve = resolve);
 		await this.changeState((state) => { return { messageBoxes: state.messageBoxes.concat([messageBox]) }; });
		return await messageBox.promise;
	}

	renderMessageBoxes() {
		return this.state.messageBoxes.map((messageBox,index) => {
			let handleRequestClose = async () => {
				messageBox.remove();
				messageBox.resolve();
			};
			let handleAction = async (value) => {
				messageBox.remove();
				messageBox.resolve(value);				
			};
			return <MessageBox key={index} open={true} {...messageBox.props} onRequestClose={handleRequestClose} onAction={handleAction} />;
		});
	}

	render() {
		if(this.state.error)
			return <Crash error={this.state.error} />;
		let routeProps = {
			services: this.services,
		};
		return (
			<StoreProvider value={this.services.store}>
				{this.renderMessageBoxes()}
				<Busy show={this.state.busyCount} block={this.state.busyBlockCount} />
				<Router>
					<>				
						<Route render={withProps(CheckUserInfo,routeProps)} />
						<Switch>
							<Route path="/login" render={withProps(Login,routeProps)} />
							<Route path="/methodics_list" render={withProps(MethodicsList,routeProps)} />
							<Route path="/journal/:methodic_id" render={withProps(Journal,routeProps)} />
							<Route path="/page/:methodic_id/:page_id" render={withProps(Page,routeProps)} />
							<Route path="/calibrations/:methodic_id/:page_id" render={withProps(Calibrations,routeProps)} />
							<Redirect to="/methodics_list" />
						</Switch>
					</>
				</Router>
			</StoreProvider>
		);
	}
}