import React from 'react';
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from '@material-ui/core';

export default
class MessageBox extends React.Component {
	renderActions() {
		if(!this.props.actions || this.props.actions.length == 0)
			return;
		let renderedActions = this.props.actions.map((action,index) => {
			let { caption, onClick, value, ...props } = action;
			let handleOnClick = (event) => {
				onClick && onClick(event);
				if(value !== undefined)
					this.props.onAction && this.props.onAction(value);
			}
			return <Button key={index} {...props} onClick={handleOnClick}>{caption}</Button>;
		});
		return <DialogActions>{renderedActions}</DialogActions>;
	}

	render() {
		let renderedActions = this.renderActions();
		return (
			<Dialog open={this.props.open} onClose={this.props.onRequestClose}>
				{this.props.title && <DialogTitle>{this.props.title}</DialogTitle>}
				{this.props.content && 
					<DialogContent>
						<DialogContentText>
							{this.props.content}
						</DialogContentText>
					</DialogContent>
				}
				{renderedActions}				
			</Dialog>
		);
	}
}