import React from 'react';
import { Listener } from '../store';
import { Grid, TextField, Typography, Checkbox, FormControlLabel, Button } from '@material-ui/core';
import { Redirect } from 'react-router';
import { Center, Panel } from '../ui';
import css from './styles.css';

export default
class Login extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			login: '',
			password: '',
			remember: true,
		};
	}

	componentDidUpdate(prevProps,prevState) {
		if(!prevState.badLoginPassword && this.state.badLoginPassword && this.nodeLogin)
			this.nodeLogin.focus();
	}

	refLogin = (node) => {
		this.nodeLogin = node;
	}

	render() {
		if(this.state.userInfo && this.state.userInfo.loggedIn)
			return <Redirect to="/" />;
		let rememberCheckboxClasses = {
			root: css.rememberCheckboxRoot,
		}
		return <>			
			<Listener name="userInfo" onChange={this.handleUserInfoChange} />
			<Center hor ver>
				<Panel>				
					<Center hor><Typography variant="headline">QControl</Typography></Center>
					<Center hor><Typography variant="body2">Лабораторный журнал</Typography></Center>
					<br /><br />
					<Center hor><Typography variant="body2"><b>Вход</b></Typography></Center>
					<TextField inputRef={this.refLogin} label="Логин" margin="dense" name="user" value={this.state.login} onChange={this.handleLoginChange} error={this.state.badLoginPassword} />
					<br />
					<TextField type="password" label="Пароль" margin="dense" name="password" value={this.state.password} onChange={this.handlePasswordChange} onKeyDown={this.handlePasswordKeyDown} error={this.state.badLoginPassword} />
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Button variant="outlined" size="small" color="primary" onClick={this.handleEnterClick}>{'Войти'}</Button>	
						</Grid>
						<Grid item xs={false}>
							<FormControlLabel classes={rememberCheckboxClasses} control={<Checkbox color="primary" checked={this.state.remember} onChange={this.handleRememberChange} />} label="Запомнить" />					
						</Grid>
					</Grid>
				</Panel>
			</Center>
		</>;
	}

	handleRememberChange = (event) => {
		this.setState({ remember: event.target.checked });
	}

	handleLoginChange = (event) => {
		this.setState({ login: event.target.value, badLoginPassword: false });
	}

	handlePasswordChange = (event) => {
		this.setState({ password: event.target.value, badLoginPassword: false });
	}

	handlePasswordKeyDown = (event) => {
		if(event.keyCode == 13)
			this.handleEnterClick();
	}

	handleEnterClick = async (event) => {
		await this.props.services.busy(async () => {
			let success = await this.props.services.client.request('login',{
				login: this.state.login,
				password: this.state.password,
			});
			if(success) {
	                        this.props.services.store.reload('userInfo');
			} else {
				this.setState({ badLoginPassword: true });
			}			
		});		
	}

	handleUserInfoChange = (userInfo) => {
		this.setState({ userInfo });
	}
}