import React from 'react';
import css from './styles.css';
import cn from 'classnames';
import { Grid } from '@material-ui/core';

export default
class Center extends React.Component {
	render() {		
		let classes = {
			container: cn({
				[css.containerHor]: this.props.hor,
				[css.containerVer]: this.props.ver,
			}),
			item: css.item,
		};
		return (
			<Grid container classes={classes} alignItems="center" justify="center">
				<Grid item classes={classes} xs={false}>{this.props.children}</Grid>
			</Grid>
		);
	}
}