let requests = require('../backend/requests');

export default
class FakeClient {
	constructor({path}) {
		let createSession = () => {
			let session = {};
			Object.defineProperty(session,'destroy',{
				value: (callback) => {
	                        	this.context.session = createSession();
					Promise.resolve().then(callback);
				},
			});
			return session;
		}
		this.context = {
			session: createSession(),
		};
		let data = localStorage.getItem('fake_session');
		if(data) {
			try {
				Object.assign(this.context.session,JSON.parse(data));
			} catch(error) {};
		}
		this.api = {};
	}

	async request(method,...params) {
		let request =   requests[method];
		if(!request)
			throw new Error('Wrong method: '+method+'.');
		try {
			return await request(this.api,this.context,...params);
		} finally {
			localStorage.setItem('fake_session',JSON.stringify(this.context.session));
		}
		
	}
}
