let symbol = Symbol();

export default function(target) {
	if(target.prototype.changeState !== undefined)
		throw new Error('Incompatible mixin: target has "changeState".');

	let oldComponentWillUnmount = target.prototype.componentWillUnmount;
	target.prototype.componentWillUnmount = function() {
		this[symbol] = true;
		return oldComponentWillUnmount && oldComponentWillUnmount.apply(this,arguments); 
	}

	target.prototype.changeState = function(change) {
		return new Promise((resolve,reject) => !this[symbol] && this.setState(change,resolve));
	}
}