import React from 'react';
import { Redirect } from 'react-router';
import { Listener } from '../store';

export default
class CheckUserInfo extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	render() {		
		if(this.state.userInfo && !this.state.userInfo.loggedIn && this.props.location.pathname != '/login')
			return <Redirect to="/login" />;
		return <Listener name="userInfo" onChange={this.handleUserInfoChange} />;
	}

	handleUserInfoChange = (userInfo) => {
		this.setState({ userInfo });
	}
}