import React from 'react';
import { Listener } from '../store';
import { Grid, TextField, Typography, Button, Table, TableHead, TableRow, TableCell, TableBody  } from '@material-ui/core';
import { Lock, LockOpen  } from '@material-ui/icons';
import { Center, Panel, Hr, Busy, AddButton, BackButton } from '../ui';
import { Link } from 'react-router-dom';

import css from './styles.css';

let styles = {
	headerRow: {
		height: '32px',
	},
	row: {
		height: '32px',
		cursor: 'pointer',
	},	
	headerCell: {
		padding: 0,
	},
	cell: {
		padding: 0,
	},
	inlineBlock: {
		display: 'inline-block',
	},
	marginLeft: {
		marginLeft: '8px',
	},
	lock: {
		marginRight: '-4px',
		cursor: 'pointer',
	},
}

export default
class Calibrations extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			calibrations: null,
		};
	}

	renderCharacters() {
		if(!this.state.calibrations)
			return;
		return this.state.calibrations.characters.map((character) => {
			let formulaStyle = {};
			if(character.old)
				formulaStyle.color = '#b9b9b9';	
			let cellStyle = { ...styles.cell };			
			let handleRowClick = (event) => {
				//this.props.history.push('/character/'+this.props.match.params.methodic_id+'/'+this.props.match.params.page_id+'/'+character.id);
			}
			let handleContextMenu = async (event) => {
				event.preventDefault();
				let action = await this.props.services.messageBox({
					title: 'Вы уверены, что хотите удалить показатель?',
					content: probe.name,
					actions: [
						{ value: 'delete', caption: 'Удалить' },
						{ value: 'cancel', caption: 'Отмена' },
					],
				});
				if(action == 'delete')
					this.handleDeleteCharacter(character.id);
			}
			return (
				<TableRow hover key={character.id} style={styles.row} onClick={handleRowClick} onContextMenu={handleContextMenu}>
					<TableCell style={cellStyle}>{character.name}</TableCell>
					<TableCell style={{ ...cellStyle, ...formulaStyle }}>{character.formula}</TableCell>					
				</TableRow>
			);
		});		
	}	

	render() {
		let renderedCharacters = this.renderCharacters();
		return <>
			<Busy show={this.state.calibrations == null} block />
			<Listener reload name={'calibrations.'+this.props.match.params.page_id} onChange={this.handleCalibrationsChange} />
			<Center hor ver>
				<Panel width="360px" maxWidth="100%">				
					<Center hor><Typography variant="headline">{'Градуировки'}</Typography></Center>
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography style={styles.inlineBlock} variant="body2">{'Дата'}</Typography>
							<Typography style={{ ...styles.inlineBlock,...styles.marginLeft }} variant="body2"><b>{this.state.calibrations && this.state.calibrations.date}</b></Typography>
						</Grid>
						<Grid item xs={false}>
							<Typography style={styles.inlineBlock} variant="body2">{'Смена'}</Typography>							
							<Typography style={{ ...styles.inlineBlock,...styles.marginLeft }} variant="body2"><b>{this.state.calibrations && this.state.calibrations.shiftNo}</b></Typography>
						</Grid>
					</Grid>					
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<BackButton component={Link} to={'/page/'+this.props.match.params.methodic_id+'/'+this.props.match.params.page_id} />
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{'Показатели'}</b></Typography>	
						</Grid>
						<Grid item xs={false}>
							<AddButton onClick={this.handleAddClick} />
						</Grid>
					</Grid>					
					<Hr />
					<Table>
						<TableHead>
							<TableRow style={styles.headerRow}>
								<TableCell style={styles.headerCell}>{'Наименование'}</TableCell>
								<TableCell style={styles.headerCell}>{''}</TableCell>
							</TableRow>
						</TableHead>
						{renderedCharacters && renderedCharacters.length != 0 &&
							<TableBody>
								{renderedCharacters}
							</TableBody>
						}
					</Table>
				</Panel>
			</Center>
		</>;
	}

	handleCalibrationsChange = (calibrations) => {
		this.setState({ calibrations });
	}

	handleDeleteCharacter = async (id) => {
		
	}
};