import React from 'react';
import { Consumer }  from './context';

export default
class Listener extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			name: props.name,
			store: props.store,
		};
	}

	componentDidMount() {
		if(this.state.store) {
			if(this.props.reload || this.props.forceReload)
				this.state.store.reload(this.state.name,this.props.forceReload);
			this.state.store.on(this.state.name,this.handleChange);
		}
	}

	componentWillUnmount() {
		if(this.state.store)
			this.state.store.off(this.state.name,this.handleChange);
	}

	handleChange = (...params) => {
		this.setState({ params });
		this.props.onChange && this.props.onChange.apply(null,params);
	}

	renderByContext = (store) => {
		if(!store)
			return null;
		return <Listener store={store} {...this.props}>{this.props.children}</Listener>;
	}

	renderByProp() {
		if(!this.state.value)
			return null;
		let valueProps = {
			[this.props.propName || this.state.name]: this.state.value,
		};
		return React.Children.map(this.props.children,(child) => React.cloneElement(child,valueProps));
	}

	render() {
		return this.state.store ? this.renderByProp() : <Consumer>{this.renderByContext}</Consumer>;
	}
}
