import React from 'react';
import { Listener } from '../store';
import { Grid, Typography, Button, List, ListItem, ListItemText, ListItemIcon, Collapse } from '@material-ui/core';
import { ExpandLess, ExpandMore, Send, FormatIndentIncrease, Code, ExitToApp  } from '@material-ui/icons';
import { Center, Panel, Hr, Busy } from '../ui';
import { Link } from 'react-router-dom';

const commonStyle = {
	paddingTop: '4px',
	paddingBottom: '4px',
	paddingLeft: '0px',
	paddingRight: '0px',
}

const styles = {
	group: { ...commonStyle, ...{

	}},
	rootItem: { ...commonStyle, ...{

	}},
	nestedItem: { ...commonStyle, ...{
		paddingLeft: '32px',
	}},
}

export default
class MethodicsList extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			quality: true,
			openedGroups: {},
			methodics: null,
		};
	}

	renderMethodic(methodic,inGroup) {
		return (
			<ListItem key={methodic.id} style={inGroup ? styles.nestedItem : styles.rootItem} component={Link} to={'/journal/'+methodic.id} button>
				<ListItemIcon><Send /></ListItemIcon>
				<ListItemText primary={methodic.name} />
			</ListItem>
		);
	};

	renderMethodics() {
		let methodics = this.state.methodics.filter((methodic) => !methodic.group && !!methodic.quality == !!this.state.quality);
		let groups = this.state.methodics.filter((methodic) => methodic.group);
		let groupsMap = {};
		for(let group of groups)
			groupsMap[group.id] = [];
		let rootGroup = [];
		for(let methodic of methodics)
			methodic.parent != null ? groupsMap[methodic.parent].push(methodic) : rootGroup.push(methodic);
		let renderedResult = [];
		for(let group of groups) {
			if(groupsMap[group.id].length == 0)
				continue;
			let renderedMethodics = groupsMap[group.id].map((methodic) => this.renderMethodic(methodic,true));
			renderedResult.push(
				<ListItem key={'group'+group.id} style={styles.group} button onClick={this.handleGroupClick.bind(this,group.id)}>
					<ListItemIcon><FormatIndentIncrease /></ListItemIcon>
					<ListItemText primary={group.name} />
					{this.state.openedGroups[group.id] ? <ExpandLess /> : <ExpandMore />}
				</ListItem>
			);
			renderedResult.push(
				<Collapse key={'collapse'+group.id} in={this.state.openedGroups[group.id]}>
					<List disablePadding>
						{renderedMethodics}
					</List>
				</Collapse>				
			);
		}
		for(let methodic of rootGroup)
			renderedResult.push(this.renderMethodic(methodic,false));
		return renderedResult;
	}

	render() {
		let renderedMethodics;
		if(this.state.methodics != null)
			renderedMethodics = this.renderMethodics();
		return <>
			<Busy show={this.state.methodics == null} block />
			<Listener reload name="methodics" onChange={this.handleMethodicsChange} />
			<Center hor ver>
				<Panel width="480px" maxWidth="100%">				
					<Center hor><Typography variant="headline">Методики</Typography></Center>
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Button variant="fab" mini onClick={this.handleExitClick}><ExitToApp /></Button>
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{this.state.quality ? 'Количественные' : 'Качественные'}</b></Typography>	
						</Grid>
						<Grid item xs={false}>
							<Button variant="fab" mini onClick={this.handleTypeChangeClick}><Code /></Button>	
						</Grid>
					</Grid>					
					<Hr />
					<List disablePadding>
						{renderedMethodics}
					</List>
				</Panel>
			</Center>
		</>;
	}

	handleMethodicsChange = (methodics) => {
		this.setState({ methodics });
	}

	handleGroupClick = (group) => {
		let openedGroups = { ...this.state.openedGroups };
		openedGroups[group] ? delete openedGroups[group] : openedGroups[group] = true;
		this.setState({ openedGroups });
	}

	handleTypeChangeClick = () => {
		this.setState({ quality: !this.state.quality });
	}

	handleExitClick = async () => {
		await this.props.services.client.request('exit');
		this.props.services.store.reload('userInfo');
	}
};