import React from 'react';
import ReactDOM from 'react-dom';
import Application from './application';
import { Crash } from './ui';
import onload from './onload';
import stripJSONComments from 'strip-json-comments';

onload(async function() {
	try {
		let response = await fetch('config.json');
		if(response.status != 200)
			throw new Error('Невозможно загрузить настройки из config.json');
		let text = stripJSONComments(await response.text());
		let config = JSON.parse(text);
		ReactDOM.render(<Application config={config} />,document.getElementById('content'));
	} catch(error) {
		ReactDOM.render(<Crash error={error} />,document.getElementById('content'));
	}	
})