export default
function createLoader(load,options) {
	options = Object.assign({
		repeatOnErrorTimeout: null,
		alwaysLoad: false,
	},options);
	return function(next,param,current) {
		if(!options.alwaysLoad && current !== undefined)
			return;
		let canceled = false;
		let doLoad = async function() {
			let result;
			while(!canceled) {
				try {
					result = await load(param,current);
					break;					
				} catch(error) {
					if(options.repeatOnErrorTimeout == null)
						return;
					await new Promise((resolve) => setTimeout(resolve,options.repeatOnErrorTimeout));
				}
			};
			if(!canceled)
				next(result);
		};
		doLoad();
		return () => canceled = true;
	}
}